:root {
    --dark-1 : #000000;
    --dark-2 : #404040;
    --dark-3 : #7f7f7f;
    --dark-4 : #bfbfbf;
    --dark-5 : #ffffff;
}

* {
    margin:0;
    padding:0;
    box-sizing:border-box;
}

body {
    font-family:Arial, Helvetica, sans-serif;
    font-size:18px;
    background-color:var(--dark-4);
}


/* Header */
header{
    display:flex;
    justify-content: center;
    border-bottom:solid 1px var(--dark-1);
    background:var(--dark-2);
}

.header-title{
    font-size:1.2em;
    background-color:var(--dark-4);
    border-radius:25px;
    display:inline-block;
    color:var(--dark-1);
    padding:5px 20px;
}

/* File Picker Input */

.file-picker-input-container{
    max-width:1080px;
    min-width:750px;
    width: 95%;
    margin:auto;
    margin-top:5px;
    display:flex;
    justify-content:flex-end;
}

.file-picker-input{
    padding:5px 20px;
    border:solid 3px var(--dark-2);
    border-radius:5px;
    background:var(--dark-5);
}

.file-picker-input input,button{
    font-size:0.8em;
    padding:2px 5px;
}

/* Form Container */
.form-container{
    max-width:1080px;
    min-width:750px;
    width: 95%;
    margin:auto;
    margin-top:25px;
    padding:10px 0;
    border:solid 3px var(--dark-2);
    border-radius:5px;
    background-color:var(--dark-5);
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
}

/* Form Container Head */

.form-container-head{
    width:100%;
    padding:15px 20px;
    display:flex;
    justify-content: space-around;
    align-items:center;
}

.form-container-head .truck-no{
    display:flex;
    flex-direction:column;
    align-items:flex-start;
    gap:3px;
}

.form-container-head .truck-no input{
    padding:2px;
    font-size:0.8em;
    max-width:10em;
}

.form-container-head .period{
    display:flex;
    flex-direction:column;
    align-items:flex-start;
    gap:3px;
}

.form-container-head .period>div{
    display:flex;
    justify-content:space-between;
    align-items:center;
    gap:10px;
    width:100%;
}

.form-container-head .period .year input{
    padding:2px;
    font-size:0.8em;
    max-width:4em;
}

/* Form transaction list */

.transactions-list{
    width:95%;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
}

.form-field,.transactions-list-header{
    width:100%;
    display:grid;
    grid-template-columns: minmax(60px, auto) minmax(160px, 1fr) minmax(120px, 1fr) repeat(2, minmax(140px, 1fr)) minmax(80px, auto);
}

.transactions-list-header{
    font-size:1.1em;
    font-weight:bold;
}

.transactions-list-header>div{
    font-size:1em;
    font-weight:bold;
    border:solid 1px;
    display:flex;
    justify-content:flex-start;
    align-items:center;
    padding:5px;
    background:var(--dark-4);
}

.form-field>div{
    width:100%;
    border:solid 1px;
    display:flex;
    justify-content:center;
    align-items:center;
    padding:5px;
}

.form-field input{
    font-size:1em;
}

.form-field .field-input-toll-name{
    display:flex;
    flex-direction:column;
    gap:5px;
}

.form-field .field-input-toll-name select{
    font-size:0.85em;
    padding:3px;
    margin:2px;
    width:100%;
    max-width:180px;
    max-height:50px;
}

.form-field .field-input-toll-name input{
    max-width:90%;
    padding:2px 5px;
}

.form-field .field-input-amount input{
    max-width:7em;
}

.form-field .delete-form-field button{
    border:none;
    background:none;
    cursor:pointer;
    font-size:1.2em;
}

.form-field .delete-form-field button:hover{
    font-size:1.4em;
}

/* Form Container Foot */

.form-container-foot{
    width:100%;
    margin-top:8px;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    gap:15px;
}

.form-container-foot button{
    font-size:1.1em;
    padding:5px 10px;
    border:none;
    border-radius:5px;
    cursor:pointer;
}

.add-form-field{
    background-color:#ffe02e;
}

.add-form-field:hover{
    scale:1.05;
}

.add-form-field:active{
    scale:0.98;
}

.submit-form{
    background:#1b55dc;
    color:var(--dark-5);
    width:90%;
}

.submit-form:hover{
    scale:1.02;
}

.submit-form:active{
    scale:0.98;
}